import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { HttpErrorResponse } from "@angular/common/http";
import * as $ from "jquery";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { CommonService } from 'src/app/core/services/common-service';
import { DiscussionForumService } from 'src/app/core/services/discussion-forum.service';
import { ActivatedRoute } from '@angular/router';
import { PostLike } from 'src/app/Models/postlike.model'
@Component({
  // selector: 'app-create-quote',
  templateUrl: "./post-dashboard.component.html",
  styleUrls: ["./post-dashboard.component.css"],
})
export class PostDashboardComponent implements OnInit {
  id: any;
  content: any;
  objPostLike = new PostLike();
  respone: any;
  downvote: any;
  aaa: any;
  name: any;
  url: any;
  constructor(
    public datepipe: DatePipe,
    private createQuoteService: CreateQuoteService,
    private commonService: CommonService,
    private discussionforumService: DiscussionForumService,
    private _Activatedroute:ActivatedRoute)
    {
    }

  ngOnInit()
  {
    this.id = this._Activatedroute.snapshot.paramMap.get("item.id");
    this.content = this._Activatedroute.snapshot.paramMap.get("item.content");
    this.name = localStorage.getItem('section');
    this.url = localStorage.getItem('url');
    this.getAllPosts(this.id);
    console.log(this.id);
  }

  postDataList: any;
  getAllPosts(id)
  {
    this.discussionforumService
    .getAllPostDashboardDataByTopicID(id)
    .subscribe(
    (res) => 
    {
      this.postDataList = res;
      console.log(this.postDataList);
    },
    (error) =>
    {
      
    }
    );
  }

  postDownVote(postID: any)
  {
    this.objPostLike.id = postID;
    this.objPostLike.downvote = 1;
    this.objPostLike.upvote = 0;
    this.discussionforumService.
    postLikeAndDisLike(this.objPostLike)
    .subscribe(
    (res) => 
    {
      this.respone=res;
      this.getAllPosts(this.id);
    });
  }

  postUpVote(postID: any)
  {
    this.objPostLike.id = postID;
    this.objPostLike.upvote = 1;
    this.objPostLike.downvote = 0;
    this.discussionforumService.
    postLikeAndDisLike(this.objPostLike)
    .subscribe(
    (res) => 
    {
      this.respone=res;
      this.getAllPosts(this.id);
    });
  }

  postRate(postID: any,postRate: any)
  {
    this.objPostLike.id = postID;
    this.objPostLike.rate = postRate;
    this.discussionforumService.
    postRate(this.objPostLike)
    .subscribe(
    (res) => 
    {
      this.respone=res;
      this.getAllPosts(this.id);
    });
  }
}